import React, { useState, useCallback, useMemo } from 'react';
import { WorkProps } from '../../../types';
import PageNavigation from '../PageNavigation/PageNavigation';
import { getTranslations, interpolate } from '../../../i18n';

import './Work.css';

import html5 from '../../../img/stack/html5.webp';
import css3 from '../../../img/stack/css3.webp';
import aws from '../../../img/stack/aws.webp';
import console from '../../../img/stack/console.webp';
import firebase from '../../../img/stack/firebase.webp';
import github from '../../../img/stack/github.webp';
import javascript from '../../../img/stack/javascript.webp';
import nextjs from '../../../img/stack/next-js.webp';
import mongodb from '../../../img/stack/mongodb.webp';
import nodejs from '../../../img/stack/nodejs.webp';
import npm from '../../../img/stack/npm.webp';
import php from '../../../img/stack/php.webp';
import postgresql from '../../../img/stack/postgresql.webp';
import python from '../../../img/stack/python.webp';
import react from '../../../img/stack/react.webp';
import redux from '../../../img/stack/redux.webp';
import stackoverflow from '../../../img/stack/stackoverflow.webp';
import tailwind from '../../../img/stack/tailwind.webp';
import vscode from '../../../img/stack/vscode.webp';
import webpack from '../../../img/stack/webpack.webp';
import wordpress from '../../../img/stack/wordpress.webp';
import moralis from '../../../img/stack/moralis.webp';

// Tech stack data - defined outside component to avoid recreation on each render
const TECH_DATA = [
  { name: 'HTML5', img: html5, category: 'frontend' },
  { name: 'CSS3', img: css3, category: 'frontend' },
  { name: 'Tailwind CSS', img: tailwind, category: 'frontend' },
  { name: 'JavaScript', img: javascript, category: 'frontend' },
  { name: 'React', img: react, category: 'frontend' },
  { name: 'Next.js', img: nextjs, category: 'frontend' },
  { name: 'Redux', img: redux, category: 'frontend' },
  { name: 'NodeJS', img: nodejs, category: 'backend' },
  { name: 'Python', img: python, category: 'backend' },
  { name: 'PHP', img: php, category: 'backend' },
  { name: 'WordPress', img: wordpress, category: 'backend' },
  { name: 'MongoDB', img: mongodb, category: 'database' },
  { name: 'PostgreSQL', img: postgresql, category: 'database' },
  { name: 'SHELL', img: console, category: 'tools' },
  { name: 'NPM', img: npm, category: 'tools' },
  { name: 'Stack Overflow', img: stackoverflow, category: 'tools' },
  { name: 'VS Code', img: vscode, category: 'tools' },
  { name: 'Webpack', img: webpack, category: 'tools' },
  { name: 'GitHub', img: github, category: 'tools' },
  { name: 'Firebase', img: firebase, category: 'cloud' },
  { name: 'AWS', img: aws, category: 'cloud' },
  { name: 'Moralis', img: moralis, category: 'cloud' },
];

const Work: React.FC<WorkProps> = ({ setDialogText, setCurrentAction, lang }) => {
  const t = getTranslations(lang);
  const [activeCategory, setActiveCategory] = useState('all');

  const categoryDescriptions = useMemo(() => ({
    all: t.work.categoryDescriptions.all,
    frontend: t.work.categoryDescriptions.frontend,
    backend: t.work.categoryDescriptions.backend,
    database: t.work.categoryDescriptions.database,
    tools: t.work.categoryDescriptions.tools,
    cloud: t.work.categoryDescriptions.cloud,
  }), [t]);

  const filteredTech = useMemo(() =>
    activeCategory === 'all'
      ? TECH_DATA
      : TECH_DATA.filter((tech) => tech.category === activeCategory),
    [activeCategory]
  );

  // Memoized category click handlers
  const handleCategoryAll = useCallback(() => setActiveCategory('all'), []);
  const handleCategoryFrontend = useCallback(() => setActiveCategory('frontend'), []);
  const handleCategoryBackend = useCallback(() => setActiveCategory('backend'), []);
  const handleCategoryDatabase = useCallback(() => setActiveCategory('database'), []);
  const handleCategoryTools = useCallback(() => setActiveCategory('tools'), []);
  const handleCategoryCloud = useCallback(() => setActiveCategory('cloud'), []);

  const handleViewProjects = useCallback(() => {
    setCurrentAction?.('goTo-portfolio');
    setDialogText?.(t.work.showTechnologies);
  }, [setCurrentAction, setDialogText, t.work.showTechnologies]);

  // Memoize random download count to prevent re-render changes
  const downloadCount = useMemo(() => Math.floor(Math.random() * 999999) + 100000, []);

  return (
    <div id="work" className="retro-page">
      <PageNavigation currentSection="goTo-work" onNavigate={setCurrentAction} lang={lang} />

      <div className="page-content">
        <h1 className="work-title">{t.work.title}</h1>

        <div className="work-description-box retro-box" style={{ marginBottom: '20px' }}>
          <p>{categoryDescriptions[activeCategory]}</p>
        </div>
        {/* Category Tabs */}
        <div className="work-category-tabs category-tabs">
          <div
            className={`category-tab ${activeCategory === 'all' ? 'active' : ''}`}
            onClick={handleCategoryAll}
          >
            {t.work.categories.all}
          </div>
          <div
            className={`category-tab ${activeCategory === 'frontend' ? 'active' : ''}`}
            onClick={handleCategoryFrontend}
          >
            {t.work.categories.frontend}
          </div>
          <div
            className={`category-tab ${activeCategory === 'backend' ? 'active' : ''}`}
            onClick={handleCategoryBackend}
          >
            {t.work.categories.backend}
          </div>
          <div
            className={`category-tab ${activeCategory === 'database' ? 'active' : ''}`}
            onClick={handleCategoryDatabase}
          >
            {t.work.categories.database}
          </div>
          <div
            className={`category-tab ${activeCategory === 'tools' ? 'active' : ''}`}
            onClick={handleCategoryTools}
          >
            {t.work.categories.tools}
          </div>
          <div
            className={`category-tab ${activeCategory === 'cloud' ? 'active' : ''}`}
            onClick={handleCategoryCloud}
          >
            {t.work.categories.cloud}
          </div>
        </div>

        <div className="work-tech-stack stack">
          {filteredTech.map((tech, index) => (
            <div key={index} className="work-tech-item tech-item">
              <img src={tech.img} alt={tech.name} title={tech.name} />
              <div style={{ fontSize: '10px', fontWeight: 'bold', marginBottom: '5px' }}>
                {tech.name}
              </div>
            </div>
          ))}
        </div>

        <div className="content-section" style={{ marginTop: '30px' }}>
          <div style={{ textAlign: 'center' }}>
            <button
              className="retro-button primary"
              onClick={handleViewProjects}
            >
              ðŸ’¼ {t.work.viewProjects}
            </button>
            <p>
              {t.work.followOnGithub}&nbsp;
              <a
                href="https://github.com/mawmawmaw/"
                target="_blank"
                rel="noreferrer"
                style={{ color: '#0066cc', textDecoration: 'underline' }}
              >
                GitHub
              </a>
            </p>
          </div>
        </div>
        {/* Download Counter */}
        <div
          className="work-counter visitor-counter"
          style={{ margin: '20px auto', textAlign: 'center', display: 'table' }}
        >
          {interpolate(t.work.totalDownloads, { count: downloadCount })}
        </div>
      </div>
    </div>
  );
};

export default Work;
